.main-navigation {
	display: block;

	font-family: $font__sans;
	font-weight: 400;
	clear: left;

	ul {
		display: none;
		list-style: none;
		margin: 0;
		padding-top: 1em;
		padding-left: 0;

		ul {
			display: none;
			top: 1.5em;
			z-index: 99999;

			ul {
				top: 0;
			}

			li {
				padding-left: 1em;

				&:hover > ul,
				&.focus > ul {
					left: 100%;
				}
			}

			a {
				width: 200px;
			}
		}

		li:hover > ul,
		li.focus > ul {
			left: auto;
		}
	}

	li {
		position: relative;
	}

	a {
		display: inline-block;
		width: 100%;
		padding: .5em 1em .5em 0;
		text-decoration: none;
		color: $color__text-main;
	}

//rbtm
	// a:hover,
	// a:focus {
	// 	text-decoration: underline;
	// }
 .dropdown-toggle.toggled-on:focus {
	 text-decoration:underline;
 }

	.current_page_item > a,
	.current-menu-item > a,
	.current_page_ancestor > a,
	.current-menu-ancestor > a {
	}

	.menu-item-has-children, .menu-item-object-custom:last-child,
	.page_item_has_children {
		min-width: 218px;
	}
	.menu-item-has-children > a,
	.page_item_has_children > a {
		padding-right: 2em;
	}
}
// end of navigation

button.dropdown-toggle {
	position: absolute;
	right: 0;
	border: none;
	background: inherit;
	color: $color__text-main;
	line-height: 1.5em;
	padding: .4em 1em .4em .5em;
}

.menu-toggle {
	position: absolute;
	top: 0;
	right: 0;
	display: block;
	margin: 1.2em 1.2em 0 0;
	padding: .6em .8em;
	font-size: 80%;
	text-transform: uppercase;
	color: $color__link;
	border: 1px solid hsla(0, 0%, 100%, .3);
}

/* Toggle small menu and children on */
.toggled-on ul,
.sub-menu.toggled-on {
	display: block;
}

@media screen and (min-width: $query__small) {
	.menu-toggle {
		display: none;
	}

	.main-navigation {

		.menu-item-has-children > a,
		.page_item_has_children > a {
			padding-right: 2em;
			background: hsla(0, 0%, 100%, .1);
		}

		ul {
			display: block;
			display: flex;
			flex-wrap: wrap;

			ul {
				flex-direction: column;
				background: hsla(0, 0%, 100%, .1);
				margin-left: 0;

				li {
					padding-left: 0;

					a {
						width: 218px;
						background: none;
					}
				}
			}

			li {

				a {
					padding: .4em 1em;
				}
			}
		}

	}

}

@media screen and (min-width: $query__medium) {
	.main-navigation {

		ul {
			justify-content: flex-end;
			padding-top: 0;

			/* If you want dropdowns to open on hover, uncomment this: */
			/*
			li:hover > ul,
				li:focus > ul {
				display: block;
			}
			*/
		}

	}
}

.main-navigation li a:hover{
	font-weight: 600;
}

//hamburger

.hamburger {
  cursor: pointer;
}
.bar1, .bar2, .bar3 {
	width: 40px;
	    height: 3px;
	    background-color: gray;
	    margin: 6px 0;
	    -webkit-transition: .4s;
	    transition: .4s;
	    margin-bottom: 10px;
}
.bar1.change {
  -webkit-transform: rotate(-45deg) translate(-11px,6px);
  transform: rotate(-45deg) translate(-11px,6px);
}
.bar2.change {opacity: 0;}
.bar3.change {
  -webkit-transform: rotate(45deg) translate(-11px,-8px);
  transform: rotate(45deg) translate(-11px,-8px);
}
